body{
  background-color: black;
}

.game {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
}

.game-board{
  background-color: #fff;
  margin-bottom: 20px;;
}

.status{
  text-align: center;
  font-size: 40px;
  color: white;
  background-color: black;
  margin-bottom: 0;
  padding-bottom: 20px;
}

.square {
  width: 150px;
  height: 150px;
  border: none;
}

.square {
  margin: 2px;
  background-color: black;
  color: white;
  font-size: 50px;
}

.replay-btn{
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.021);
  color: white;
  padding: 16px 32px;
}

.replay-btn:hover{
  border: 1px solid white;
  background-color: rgba(255, 255, 255, 0.205);
  cursor: pointer;
  color: white;
  padding: 16px 32px;
}

/*Back Button CSS*/

.back-btn {
  position: fixed;
  bottom: 5px;
  right: 20px;
  z-index: 1000;
  box-shadow: 0px 10px 14px -7px #3e7327;
  background: linear-gradient(to bottom, #77b55a 5%, #265a12 100%);
  background-color: #77b55a;
  border-radius: 8px;
  display: inline-block;
  cursor: pointer;
  color: #ffffff;
  font-family: Arial;
  font-size: 20px;
  font-weight: bold;
  padding: 5px 10px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #5b8a3c;
}
.back-btn:hover {
  background: linear-gradient(to bottom, #265a12 5%, #77b55a 100%);
  background-color: #265a12;
}
